.slide {
  height: 100%;

  position: relative;

  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;

  &__wrapper {
    position: absolute;
    height: 100%;
    width: 100%;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #000000 100%);
    &-content {
      text-align: left;
      position: absolute;
      top: 380px;
      left: 96px;

      &-title {
        font-style: normal;
        font-weight: 500;
        font-size: 40px;
        line-height: 47px;
        color: #ffffff;
        margin-bottom: 8px;
      }

      &-subtitle {
        max-width: 495px;
        font-style: normal;
        font-weight: 300;
        font-size: 24px;
        line-height: 100%;
        color: #eeeeee;
        margin-bottom: 32px;
      }

      &-button {
        max-width: 164px;
      }
    }
  }
}
